.footer {
    border-top: 1px solid black;
    .container {
        .footer-list {
            display: flex;
            justify-content: center;
            column-gap: 30px;
            .footer-list__item {
                width: 40px;
                height: 40px;
            }

            .footer-list__item:hover {
                opacity: .8;
            }
        }
    }
}

@media (max-width:430px) {
    .footer {
        .container {
            max-width: 380px;
            .footer-list {
                column-gap: 20px;
                .footer-list__item {
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }
}