/* <uniquifier>: Use a unique and descriptive class name
// <weight>: Use a value from 400 to 700

.caveat-<uniquifier> {
  font-family: "Caveat", cursive;
  font-optical-sizing: auto;
  font-weight: <weight>;
  font-style: normal;
}


.concert-one-regular {
  font-family: "Concert One", sans-serif;
  font-weight: 400;
  font-style: normal;
}
*/


body, html {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

ol,ul {
  list-style: none;
}

a {
  text-decoration: none;
}

#root {
  min-height: 100vh;
}

#App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgba(233, 33, 99, .3);
  font-family: "Caveat", cursive;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}


/* General */

.container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}