.schedule {
  margin: 0 auto;
  max-width: 1200px;
  border: 1px solid white;
  border-radius: 50px;
  box-shadow: 2px 2px 4px white;
}
.schedule .schedule-title {
  text-align: center;
  font-size: 48px;
  font-weight: 500;
}
.schedule .container {
  position: relative;
  padding: 20px 0;
  display: grid;
  grid-template-columns: 500px 500px;
  justify-content: center;
  -moz-column-gap: 30px;
       column-gap: 30px;
  row-gap: 40px;
}
