.schedule .container .table-img-7:hover,
.schedule .container .table-img-8:hover {
  animation: rotate 0.5s linear;
}
.schedule .container .schedule-table {
  position: relative;
  max-width: 500px;
  max-height: 250px;
  border-radius: 20px;
  border-collapse: collapse;
}
.schedule .container .schedule-table .table-title {
  font-size: 32px;
}
.schedule .container .schedule-table tr td, .schedule .container .schedule-table tr th {
  padding: 0;
  margin: 0;
  width: 70px;
  height: 30px;
  border: 0.5px solid black;
  font-size: 22px;
  color: black;
}
.schedule .container .schedule-table tr .white {
  background-color: rgba(255, 255, 255, 0.3);
}
.schedule .container .schedule-table tr .grey {
  background-color: rgba(128, 128, 128, 0.6);
}
.schedule .container .schedule-table tr .blue {
  background-color: rgba(3, 169, 244, 0.3);
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
