.schedule {
    margin: 0 auto;
    max-width: 1200px;
    border: 1px solid white;
    border-radius: 50px;
    box-shadow: 2px 2px 4px white;

    .schedule-title {
        text-align: center;
        font-size: 48px;
        font-weight: 500;
    }
    .container {
        position: relative;
        padding: 20px 0;
        display: grid;
        grid-template-columns: 500px 500px;
        justify-content: center;
        column-gap: 30px;
        row-gap: 40px;
        .table-img-1,
        .table-img-2,
        .table-img-3,
        .table-img-4,
        .table-img-5,
        .table-img-6,
        .table-img-7,
        .table-img-8
         {
            position: absolute;
            width: 50px;
        }
        .table-img-1 {
            top: 10%;
            left: 5%;
        }
        .table-img-2 {
            top: 55%;
            left: 46%;
        }
        .table-img-3 {
            top: 10%;
            left: 90%;
        }
        .table-img-4 {
            top: 10%;
            left: 49.5%;
        }
        .table-img-5 {
            top: 67%;
            left: 5%;
        }
        .table-img-6 {
            top: 67%;
            left: 46%;
        }
        .table-img-7 {
            top: 87.5%;
            left: 49.5%;
        }
        .table-img-8 {
            top: 67.5%;
            left: 90.5%;
        }
        .table-img-1:hover,
        .table-img-2:hover,
        .table-img-3:hover,
        .table-img-4:hover,
        .table-img-5:hover,
        .table-img-6:hover,
        .table-img-7:hover,
        .table-img-8:hover {
            animation: rotate .5s linear;
        }
        .schedule-table {
            position: relative;
            max-width: 500px;
            max-height: 250px;
            border-radius: 20px;
            border-collapse: collapse;
            .table-title {
                font-size: 32px;
            }

            tr {
                td, th {
                    padding: 0;
                    margin: 0;
                    width: 70px;
                    height: 30px;
                    border: .5px solid black;
                    font-size: 22px;
                    color: black;
                }

                .white {
                    background-color: rgba($color: white, $alpha: .3);
                }

                .grey {
                    background-color: rgba($color: grey, $alpha: .6);
                }

                .blue {
                    background-color: rgba(3, 169, 244, .3);
                }
            }
        }
    }
}
@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(90deg);
    }
    50% {
        transform: rotate(180deg);
    }
    75% {
        transform: rotate(270deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

