.comments .container {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 1px solid white;
  border-radius: 50px;
  box-shadow: 2px 2px 4px white;
}
.comments .container .comments-title {
  font-size: 48px;
  font-weight: normal;
}
.comments .container .comments-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  -moz-column-gap: 20px;
       column-gap: 20px;
  row-gap: 10px;
}
.comments .container .comments-list .comments-list__item {
  width: 300px;
  height: 100px;
  border-radius: 20px;
}
.comments .container .comments-list .comments-list__item:hover {
  transform: scale(1.5);
}

@media (max-width: 700px) {
  .comments .container .comments-title {
    font-size: 38px;
  }
  .comments .container .comments-list {
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
  .comments .container .comments-list .comments-list__item {
    width: 200px;
    height: 80px;
  }
}
@media (max-width: 430px) {
  .comments .container {
    max-width: 380px;
  }
  .comments .container .comments-title {
    font-size: 24px;
  }
  .comments .container .comments-list {
    -moz-column-gap: 10px;
         column-gap: 10px;
  }
  .comments .container .comments-list .comments-list__item {
    width: 100px;
    height: 40px;
    border-radius: 10px;
  }
}/*# sourceMappingURL=comments.css.map */