.header {
  border-bottom: 1px solid black;
  padding: 5px;
  background-color: rgba(3, 169, 244, 0.3);
}
.header .container {
  display: flex;
  align-content: center;
  flex-direction: row;
  justify-content: space-between;
  text-align: left;
  padding: 5px;
}
.header .container .header-text .header-title {
  margin: 5px 0;
  font-weight: 500;
  font-size: 64px;
}
.header .container .header-text .header-title2 {
  margin: 5px 0;
  font-size: 36px;
}
.header .container .header-list__wrapper .header-title3 {
  text-align: center;
  font-size: 24px;
}
.header .container .header-list__wrapper .header-list {
  display: flex;
  align-items: center;
  -moz-column-gap: 10px;
       column-gap: 10px;
}
.header .container .header-list__wrapper .header-list .header-list__item {
  width: 50px;
  height: 50px;
}
.header .container .header-list__wrapper .header-list .header-list__item:hover {
  opacity: 0.8;
}

@media (max-width: 1000px) {
  .header .container {
    max-width: 800px;
  }
  .header .container .header-text .header-title {
    font-weight: 500;
    font-size: 48px;
  }
  .header .container .header-text .header-title2 {
    font-size: 30px;
  }
  .header .container .header-list__wrapper .header-list .header-list__item {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 430px) {
  .header .container {
    max-width: 380px;
  }
  .header .container .header-text .header-title {
    font-weight: 500;
    font-size: 24px;
  }
  .header .container .header-text .header-title2 {
    font-size: 18px;
  }
  .header .container .header-list__wrapper .header-title3 {
    font-size: 12px;
  }
  .header .container .header-list__wrapper .header-list .header-list__item {
    width: 20px;
    height: 20px;
  }
}/*# sourceMappingURL=header.css.map */