.footer {
  border-top: 1px solid black;
}
.footer .container .footer-list {
  display: flex;
  justify-content: center;
  -moz-column-gap: 30px;
       column-gap: 30px;
}
.footer .container .footer-list .footer-list__item {
  width: 40px;
  height: 40px;
}
.footer .container .footer-list .footer-list__item:hover {
  opacity: 0.8;
}

@media (max-width: 430px) {
  .footer .container {
    max-width: 380px;
  }
  .footer .container .footer-list {
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
  .footer .container .footer-list .footer-list__item {
    width: 20px;
    height: 20px;
  }
}/*# sourceMappingURL=footer.css.map */