.info .container {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 5px;
  border: 1px solid white;
  border-radius: 50px;
  box-shadow: 2px 2px 4px white;
}
.info .container .info-title {
  font-size: 48px;
  font-weight: 500;
}
.info .container .info-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  -moz-column-gap: 20px;
       column-gap: 20px;
  row-gap: 20px;
}
.info .container .info-list .info-list__item {
  display: flex;
  align-items: center;
  padding: 10px;
  width: 400px;
  height: 120px;
  border: 1px solid white;
  border-radius: 20px;
  font-size: 32px;
  text-align: left;
  background-color: rgba(3, 169, 244, 0.3);
  box-shadow: 2px 2px 10px white;
}
.info .container .info-list .info-list__item:hover {
  box-shadow: 4px 4px 6px 6px white;
}

@media (max-width: 1200px) {
  .info .container {
    max-width: 800px;
  }
  .info .container .info-list .info-list__item {
    width: 300px;
    font-size: 26px;
  }
}
@media (max-width: 700px) {
  .info .container {
    max-width: 500px;
  }
  .info .container .info-title {
    font-size: 36px;
  }
  .info .container .info-list .info-list__item {
    width: 190px;
    height: 70px;
    font-size: 18px;
  }
}
@media (max-width: 430px) {
  .info .container {
    max-width: 380px;
  }
  .info .container .info-title {
    font-size: 24px;
  }
  .info .container .info-list {
    padding: 0;
  }
  .info .container .info-list .info-list__item {
    width: 100px;
    height: 20px;
    font-size: 8px;
  }
}/*# sourceMappingURL=info.css.map */